import React from "react"
import Card from "react-bootstrap/Card"



const Skill =  ({skillName}) => (
    <Card className={"glass glass-element m-3 flex-grow-0 flex-shrink-0 flex-basis-0"}>
        <Card.Img
            aria-label={skillName}
            src={require("../images/skills/"+skillName.replace(/\s/g, '').replace(/#/g, 'S')+".png")}
            />
        <Card.Body>
            <center className="fs-4 fw-bolder">{skillName}</center>
        </Card.Body>
    </Card>
)

export default Skill;