import React from "react"
import { graphql } from "gatsby"
import Layout from "../components/layout"
//import Image from "../components/image"
import SEO from "../components/seo"
//import Skill from "../components/skill"
import { Card, Container, CardDeck } from "react-bootstrap"
import ProfileCard from "../components/profile-card"
import Skill from "../components/skill"
import ScrollContainer from "../components/scroll-container"

const IndexPage = ({
  data: {
    allSkillsJson: { edges },
  },
}) => {
  const programmingLanguage = edges
    .filter(edge => edge.node.type === "Programming Language")
    .map(edge => <Skill skillName={edge.node.name} />)
  const frameworks = edges
    .filter(edge => edge.node.type === "Frameworks")
    .map(edge => <Skill skillName={edge.node.name} />)
  const languages = edges
    .filter(edge => edge.node.type === "Languages")
    .map(edge => <Skill skillName={edge.node.name} />)
  const tools = edges
    .filter(edge => edge.node.type === "Tools")
    .map(edge => <Skill skillName={edge.node.name} />)

  return (
    <Layout>
      <SEO title="Home" />

      <Container
        className={"main d-flex align-items-center justify-content-center px-0"}
      >
        <ProfileCard />
      </Container>

      <div className={"arrow d-flex align-items-end justify-content-center"}>
        <div className={"chevron-container"}>
          <div className={"chevron"}></div>
          <div className={"chevron"}></div>
          <div className={"chevron"}></div>
        </div>
      </div>

      <div id={"skills"} className={"my-3"}>
        <Card className={"glass"}>
          <Card.Body>
            <h3>Skills</h3>
            <Container>
              Some of the programming languages I know:
              <ScrollContainer>
                {programmingLanguage}
              </ScrollContainer>
              Some of the frameworks I have used in the past:
              <ScrollContainer>
                {frameworks}
              </ScrollContainer>
              The languages I speak:
              <ScrollContainer>
                {languages}
              </ScrollContainer>
              Some of the tools I use:
              <ScrollContainer> {tools} </ScrollContainer>
            </Container>
          </Card.Body>
        </Card>
      </div>
    </Layout>
  )
}

export default IndexPage

export const pageQuery = graphql`
  query {
    allSkillsJson {
      edges {
        node {
          name
          type
        }
      }
    }
  }
`
