import React, { Component, createRef } from "react"

import { BsChevronCompactLeft, BsChevronCompactRight } from "react-icons/all"

class ScrollContainer extends Component {
  constructor(props) {
    super(props)
    this.containerRef = createRef()
  }

  handleScroll = direction => {
    if (this.containerRef && this.containerRef.current) {
      if (direction === "left") {
        this.containerRef.current.scrollLeft -= 200
      } else {
        this.containerRef.current.scrollLeft += 200
      }
    }
  }

  render() {
    return (
      <div className="d-flex align-items-center">
        <div
          role="button"
          tabIndex={0}
          className="side-chevron-container"
          onClick={() => {
            this.handleScroll("left")
          }}
        >
          <BsChevronCompactLeft />
        </div>
        <div
          ref={this.containerRef}
          className="d-flex overflow-hidden flex-grow-1 justify-content-between"
        >
          {this.props.children}
        </div>
        <div
          role="button"
          tabIndex={0}
          className="side-chevron-container"
          onClick={() => {
            this.handleScroll("right")
          }}
        >
          <BsChevronCompactRight />
        </div>
      </div>
    )
  }
}
/* 
	<div className="right-chevron" ></div> */

export default ScrollContainer
